<template>
  <div class="dashboard">
    <loading :active.sync="isLoading"></loading>   
     
    <v-card v-for="(item, index) in items" :key="index" flat class="mb-2">

        <v-card-title primary-title>
          <div>
            {{item.subtitle}}
          </div>
        </v-card-title>
      </v-card>

  <!-- <b-list-group-item   >{{item.subtitle}}</b-list-group-item> -->
  </div>
</template>

<script>
import db from './firebaseInit'
import Loading from 'vue-loading-overlay'

export default {
  name: 'Dashboard',
  components: {
    Loading
  },
  data () {
    return {
      
      items: [],
      isLoading: true
    }
  },
   created () {
      console.log(this.$route.path);

      // db.collection('sources').onSnapshot(function(querySnapshot) {
      //   querySnapshot.forEach((doc) => {
            
      //       console.log(doc.data());
      //       let data = {
      //         'subtitle': doc.data().name
      //       }
      //       let divider = { divider: true, inset: false }

      //      data.items.push(data)
      //      data.items.push(divider)
           
      //   })
      // })
    db.collection('sources').get().then((querySnapshot) => {
      this.loading = false

      querySnapshot.forEach((doc) => {
        
          let data = {
            'subtitle': doc.data().name
          }

        this.items.push(data)

        this.isLoading = false 
      })

    })


  }
}
</script>



