import Vue from 'vue'
import Router from 'vue-router'
import Dashboard from '@/components/dashboard'
import Additem from '@/components/addItem'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Dashboard',
      component: Dashboard
    },
    {
      path: '/additem',
      name: 'AddItem',
      component: Additem
    }
  ]
})



