<template>
  <div class="additem">
    
    <v-dialog v-if="select == 'book'" v-model="dialog" fullscreen transition="dialog-bottom-transition" :overlay="false">
       <loading :active.sync="isLoading"></loading>   
       <v-btn large slot="activator">ISBN Buchsuche</v-btn>
        <v-card>
        <v-toolbar dark color="primary">
          <v-btn icon @click.native="dialog = false" dark @click="(searchkey='', isbnsearch_result=[])">
            <v-icon>close</v-icon>
          </v-btn>
          <v-toolbar-title>Buchdaten suchen</v-toolbar-title>
          <v-spacer></v-spacer>
          <v-toolbar-items>
            <!-- <v-btn dark flat @click.native="dialog = false">Save</v-btn> -->
          </v-toolbar-items>
        </v-toolbar>
        <v-divider></v-divider>
        
        <v-container fluid>
              <v-layout row >
                <v-flex sm10>
                  <v-text-field label="ISBN / Name / etc." v-model="searchkey" ></v-text-field>
                </v-flex>
                <v-flex sm2>
                  <v-btn @click="searchBook()" :disabled="searchkey == ''">Suchen</v-btn>
                </v-flex>
              </v-layout>  
              <v-subheader>{{isbnsearch_result_count}} x Ergebnisse der Suche</v-subheader>
              
              <v-layout >
                  <v-list three-line style="width:100%">
                    <template v-for="item in isbnsearch_result">
                      <v-divider></v-divider>
                      
                      <v-list-tile avatar :key="item.id" >
                        <v-list-tile-avatar v-if="item.volumeInfo.imageLinks">
                          <img :src="item.volumeInfo.imageLinks.thumbnail">
                        </v-list-tile-avatar>
                        <v-list-tile-avatar v-if="!item.volumeInfo.imageLinks">
                            <img src="http://via.placeholder.com/200x200&text=Kein%20Bild">
                        </v-list-tile-avatar>

                        <v-list-tile-content>
                          <v-list-tile-title><strong>{{item.volumeInfo.title}}</strong></v-list-tile-title>
                          <v-list-tile-sub-title v-html="item.volumeInfo.subtitle"></v-list-tile-sub-title>
                          <v-list-tile-sub-title>
                            von <span v-for="(author, aindex) of item.volumeInfo.authors" v-bind:key="aindex">{{author}} <span v-if="aindex < item.volumeInfo.authors.length - 1">,&nbsp;</span></span>
                          </v-list-tile-sub-title>
                        </v-list-tile-content>
                      <v-list-tile-action>
                        <v-btn ripple @click="getBookData(item.volumeInfo)">
                         Buchdaten übernehmen
                        </v-btn>
                      </v-list-tile-action>
                      </v-list-tile>
                    </template>
                  </v-list>
              </v-layout>

        </v-container>   
       

      </v-card>
    </v-dialog>  

    <v-form class="mt-3">
        <v-subheader>Quellentyp</v-subheader>
        <v-select class="mb-4" label="Item" solo :items="items" item-text="text" item-value="value" v-model="select" required></v-select>
        
        <div v-if="select == 'book'">
          <v-subheader>Buchdaten</v-subheader>   
          <v-text-field class="mb-4" label="Titel" v-model="title" solo required></v-text-field>
          <v-text-field class="mb-4" label="Untertitel" v-model="subtitle" solo required></v-text-field>
          <v-text-field class="mb-4" label="Erscheinungsjahr" v-model="releaseyear" solo required></v-text-field>
          <v-text-field class="mb-4" label="Auflage" v-model="edition" solo required></v-text-field>
          <v-text-field class="mb-4" label="Verlag" v-model="publisher" solo required></v-text-field>
          <v-text-field class="mb-4" label="Erscheinungsort " v-model="city" solo required></v-text-field> 
        </div>
        <v-subheader>Authoren</v-subheader>
        <v-layout row>
          <v-btn large  @click="addAuthorField()">Author hinzufügen</v-btn>
        </v-layout>
    
          <v-layout >    
            <v-flex xs4 v-for="(author, index) of authors" :key="index">
              <v-card flat >
                <v-card-title primary-title> 
                    <v-text-field required class="d-block" solo v-model="author.name" v-bind:placeholder="author.label"></v-text-field>
                  
                </v-card-title>
                <v-card-actions>
                  <v-btn color="error" flat @click="removeAuthorField(index)"><v-icon>clear</v-icon> Entfernen</v-btn>
                </v-card-actions>
              </v-card>
            </v-flex>    
          </v-layout>
     
        <v-layout row class="mt-3">
          <v-btn color="primary" @click="submit">Speichern</v-btn>
        </v-layout>    

    </v-form>     
          
  </div>
</template>

<script>

import Loading from 'vue-loading-overlay'

export default {
  name: 'Additem',
  components: {
    Loading
  },
  data () {
    return {
      dialog: false,
      isLoading: false,
      searchkey: '',
      select:'book',
      title:'',
      subtitle:'',
      releaseyear:'',
      edition:'',
      publisher: '',
      city:'',
      items: [
        { value: 'book', text: 'Buch/Monografie' },
        { value: 'article', text: 'Beiträgen und Artikeln aus Handbüchern,Sammelbänden und Lexika' },
        { value: 'newspaper', text: 'Zeitschriften/Periodika' },
        { value: 'internet', text: 'Titelaufnahmen aus dem Internet' },
        { value: 'sourcesoflaw', text: 'Rechtsquellen' },
        { value: 'writedown', text: 'Niederschrift' },
      ],
      authors:[],
      isbnsearch_result:[],
      isbnsearch_result_count:0
      
     
    }
  },
  created () {
      console.log(this.$route.path);
  },
  methods:{
    addAuthorField(){
        this.authors.push({name:'', label:'Name / Vorname'})
    },
    removeAuthorField(index){
        this.authors.splice(index,1)
    },
    submit(){
      console.log(this.title)
    },
    searchBook(){
        this.isLoading = true

        this.$jsonp('https://www.googleapis.com/books/v1/volumes?', { q: ''+this.searchkey}).then(json => {
            // Success.
          console.log(json.items)
          this.isLoading = false
          this.isbnsearch_result_count = json.totalItems;
          this.isbnsearch_result = json.items;

        }).catch(err => {
            // this.$notify({
            //   type: 'error',
            //   title: 'Important message',
            //   text: err.statusText
            // });
        })
    },
    getBookData(bookdata){
        this.dialog = false
     
        let x = []
        bookdata.authors.map(function(value, key) {
            let name = []
            name = value.split(' ')
            name.reverse()

           x.push({'name':name.join(' ')})
        });
        this.authors = x
        this.title = bookdata.title
        this.subtitle = bookdata.subtitle
        this.releaseyear = bookdata.publishedDate
        this.publisher = bookdata.publisher
        
    }


  }
  
}
</script>



